nav{
	padding: 0px;
	display: flex;
	justify-content: flex-end;
}
nav p{
	margin: 4px;
	padding: 3px 8px;
	cursor: pointer;
	font-weight: bold;
	font-size: 20px;
	background: transparent;
	box-shadow: 0px 0px 2px 2px rgba(0,0,0,0.4);
}
nav p:hover{
	background-color:  rgba(160, 80, 240,0.6);
}