.img-container{
	position: relative;
	margin: auto;
	width: 500px;
	text-align: center;
}
#input-img{
	width: 500px;
	object-fit: cover; 
}
.bounding-boxes{
	z-index: 1;
	box-shadow: 0 0 0 3px #149df2 inset;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	cursor: pointer;
}